.tooltip {
  position: relative;
  margin-right: 1.8rem;
  filter: drop-shadow(0px 29px 69px rgba(12, 43, 122, 0.05)) drop-shadow(0px 18.7963px 40.4097px rgba(12, 43, 122, 0.037963)) drop-shadow(0px 11.1704px 21.9778px rgba(12, 43, 122, 0.0303704)) drop-shadow(0px 5.8px 11.2125px rgba(12, 43, 122, 0.025)) drop-shadow(0px 2.36296px 5.62222px rgba(12, 43, 122, 0.0196296)) drop-shadow(0px 0.537037px 2.71528px rgba(12, 43, 122, 0.012037));
}
.tooltip svg {
  cursor: help;
}
.tooltip__body {
  position: absolute;
  bottom: 38px;
  left: 50%;
  transform: translateX(-50%);
  width: 100vw;
  max-width: 28rem;
  padding: 1.6rem;
  background: #2c2b3c;
  border-radius: 4px;
  opacity: 0;
  z-index: -1;
  transition: opacity 0.4s 0.1s ease;
}
@media (max-width: 768px) {
  .tooltip__body {
    left: calc(100% + 3rem);
    transform: translateX(-100%);
  }
}
.tooltip__body span {
  line-height: 2rem;
}
.tooltip__tail {
  position: absolute;
  left: 50%;
  bottom: -8px;
  width: 2.2rem;
  height: 2.2rem;
  transform: translateX(-50%) rotate(45deg);
  background-color: #2c2b3c;
}
@media (max-width: 768px) {
  .tooltip__tail {
    left: calc(100% - 4rem);
  }
}
.tooltip svg:hover + .tooltip__body,
.tooltip svg:focus + .tooltip__body {
  opacity: 1;
  z-index: 1;
}

