/*common*/
.subscribe__container {
  display: flex;
  padding: 4.7rem 0;
}
@media (max-width: 960px) {
  .subscribe__container {
    flex-direction: column;
  }
}
.subscribe__content {
  width: calc(50% - 1rem);
  margin-right: auto;
}
@media (max-width: 960px) {
  .subscribe__content {
    width: 100%;
    margin-bottom: 3rem;
  }
}
.subscribe__title {
  margin-bottom: 1.5rem;
}
.subscribe__form {
  width: calc(50% - 1rem);
}
@media (max-width: 960px) {
  .subscribe__form {
    width: 100%;
  }
}
.subscribe__form form {
  display: flex;
  flex-direction: column;
}
.subscribe__form form fieldset {
  padding: 0;
  border: none;
  margin: 0;
  margin-bottom: 2.1rem;
}
.subscribe__accept {
  display: flex;
}
.subscribe__accept label {
  position: relative;
  line-height: 1.8rem;
  padding-left: 3.5rem;
  cursor: pointer;
}
.subscribe__accept label::before {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 2rem;
  height: 2rem;
  border: 1px solid #e8e6ed;
  border-radius: 4px;
  background: transparent;
}
.subscribe__accept label a {
  color: #af38d9;
}
.subscribe__accept input:checked + label::before {
  border-color: red;
}
.subscribe__accept input {
  display: none;
}

/*footer*/
.footer {
  background-color: #1E262E;
}
.footer__container {
  padding-top: 4rem;
  padding-bottom: 9.2rem;
}
.footer__top {
  margin-bottom: 4rem;
}
@media (max-width: 534px) {
  .footer__top {
    margin-bottom: 2.4rem;
  }
}
.footer__nav {
  display: flex;
  flex-wrap: wrap;
}
.footer__nav-lists {
  display: flex;
  flex-wrap: wrap;
  flex-grow: 1;
  flex-grow: 1;
}
@media (max-width: 768px) {
  .footer__nav-lists {
    order: 2;
  }
}
.footer__nav-item {
  margin-right: auto;
  padding-right: 4rem;
  margin-bottom: 4rem;
}
@media (max-width: 768px) {
  .footer__nav-item {
    width: 50%;
    max-width: 50%;
    margin-bottom: 3.2rem;
  }
}
@media (max-width: 534px) {
  .footer__nav-item {
    padding-right: 1.5rem;
  }
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  max-width: 18.5rem;
  margin-bottom: 4rem;
}
.footer__contacts button {
  color: #f8f8f9;
  width: 100%;
}
.footer__info {
  margin-bottom: 2rem;
}
.footer__info button {
  width: 100%;
}
.footer__links {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 100%;
}
.footer__social {
  display: flex;
  margin-right: auto;
  flex-wrap: wrap;
