.index {
    .search {
        padding-bottom: 6.4rem;

        &__wrapper {
            position: relative;
            max-width: 98rem;
            margin: 0 auto;
            margin-top: -3rem;
            padding: 3.2rem;
            padding-bottom: 2.4rem;
            border-radius: 4px;
            background: $white;
            box-shadow: 0px 10px 20px rgba(42, 52, 79, 0.1);
            z-index: 10;

            @media(max-width: $tablet_small) {
                max-width: 100%;
                margin-top: 0;
                padding: 0;
                padding-top: 2.4rem;
                box-shadow: none;
            }
        }

        form {
            a {
                color: $purple;
            }
        }

        &__filter {
            display: none;

            &.js-mounted {
                display: block;
                /*display: flex;
                flex-direction: column;*/
                animation: fadeIn 0.5s linear;

                /*overflow: hidden;
                overflow-x: auto;
                height: 430px;*/
            }

            /*&-result {
                position: relative;
                margin-bottom: 3.2rem;

                &-container {
                    max-height: 31.2rem;
                    overflow: hidden;
                    padding-right: 2rem;
                }

                button {
                    margin-left: auto;
                }

                &-content {
                    display: flex;

                    &-text {
                        width: 50%;
                        max-width: 45.8rem;
                        padding-right: 2rem;
                        margin-right: auto;
                    }

                    &-card {
                        width: 50%;
                        max-width: 44.8rem;
                    }
                }
            }*/
        }

        &__field {
            margin-bottom: 3.2rem;

            &--controls {
               // margin-bottom: auto;
            }

            &-title {
                display: inline-block;
                margin-bottom: 0.8rem;
            }

            &-content {
                display: flex;
                align-items: center;
                white-space: nowrap;

                &--filter {
                    align-items: flex-start;
                }

                a {
                    margin-right: 0.8rem;
                }
            }
        }

        &__control {
            margin-bottom: 2.4rem;

            input {
                padding-right: 20rem;

                @media(max-width: $tablet_small) {
                    padding-right: 7rem;
                }
            }

            svg {
                opacity: .5;
            }
        }
    }

    @keyframes fadeIn {
        0% {
            opacity: 0;
        }

        100% {
            opacity: 1;
        }
    }
}